import Vue from 'vue'
import Router from 'vue-router'

const index = r => require.ensure([], () => r(require('@/pages/index/index')), 'index')
const content = r => require.ensure([], () => r(require('@/pages/content/content')), 'content')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/content',
      name: 'content',
      component: content
    }
  ]
})



